.navbar {
  background-color: #FFC107; /* Slight orange-yellow */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.navbar a {
  text-decoration: none;
  color: #fff;
}

.home, .about {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 60px); /* Adjust for navbar height */
}

.home img {
  width: 200px;
  margin-bottom: 2rem;
}

.about {
  justify-content: center;
}

.social-links {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

/* Add random shapes here using CSS, e.g., */
.shape {
  position: absolute;
  background-color: yellow;
  border-radius: 50%;
  /* Add random sizes and positions */
}
